@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

:root {
  font-size: 16px;
}

body {
  font-family: 'Alata', sans-serif;
  color: $white;
  overflow-x: hidden;
}

.title {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 300;
  font-size: 48px;
  line-height: 48px;
  color: $black;
  text-transform: uppercase;

  @media(max-width:767px) {
    font-size: 32px;
    line-height: 32px;
    text-align: center;
  }
}

.container {
  max-width: 1110px;
  margin: 0 auto;
  padding-left: 0;
  padding-right: 0;
}

.main {
  background: linear-gradient(to top, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(../img/bg-main.png) center / cover no-repeat;
  padding-bottom: 146px;

  @media(max-width:575px) {
    padding-bottom: 227px;
  }
}

.main-box {
  max-width: 570px;
  border: 2px solid #FFFFFF;
  margin-top: 122px;
  padding: 40px 39px 28px 40px;

  @media(max-width:991px) {
    margin: 163px auto 0 auto;
  }

  @media(max-width:575px) {
    max-width: 266px;
    padding: 20px 20px 20px 20px;
  }
}

.main-box__heading {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 300;
  font-size: 72px;
  line-height: 70px;
  text-transform: uppercase;

  @media(max-width:767px) {
    font-size: 40px;
    line-height: 38px;
    max-width: 282px;
  }
}

.aboutCompany {
  margin-top: 160px;
  margin-bottom: 184px;
  position: relative;

  @media(max-width:991px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 96px;
    margin-bottom: 96px;
  }

  &-image {
    background: url(../img/bg-about-company.png) center / cover no-repeat;
    max-width: 731px;
    height: 500px;

    @media(max-width:1200px) {
      max-width: 643px;
      height: 400px;
    }

    @media(max-width:991px) {
      width: 100%;
      height: 37vh;
    }

    @media(max-width:575px) {
      width: 80%;
      height: 25vh;
    }
  }

  &-text {
    max-width: 541px;
    max-height: 317px;
    background-color: $white;
    padding: 96px 0 0 96px;
    position: absolute;
    right: -97px;
    top: 183px;

    @media(max-width:1200px) {
      max-width: 400px;
      max-height: 293px;
      padding: 46px 0 0 46px;
      right: 0px;
      top: 108px;
    }

    @media(max-width:991px) {
      padding: 0;
      position: initial;
      margin-top: 48px;
    }

    @media(max-width:767px) {
      max-width: 279px;
    }

    &__description {
      margin: 0;
      padding: 0;
      font-size: 15px;
      line-height: 25px;
      color: $black;
      opacity: 0.5;
      max-width: 445px;
      margin-top: 25px;

      @media(max-width:767px) {
        text-align: center;
      }
    }
  }
}

.creations {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 184px;

  @media(max-width:767px) {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 100px;
  }

  &-item {
    &:nth-child(2) {
      @media(max-width:767px) {
        order: 1;
        margin-top: 45px;
      }
    }

    &__link {
      font-size: 14px;
      line-height: 19px;
      letter-spacing: 5px;
      text-transform: uppercase;
      text-decoration: none;
      color: #000;
      border: 1px solid #000;
      padding: 12px 33px 6px 36px;
      transition: 0.2s ease;

      &:hover {
        background-color: $black;
        color: $white;
      }
    }

    &__image {
      display: block;
      width: 256px;
      height: 450px;
      background-position: center;
      position: relative;

      @media(max-width:1200px) {
        width: 206px;
        height: 400px;
      }

      @media(max-width:767px) {
        width: 450px;
        height: 120px;
        background-repeat: no-repeat;
        background-size: cover;
      }

      @media(max-width:575px) {
        width: 327px;
      }

      &>span {
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 300;
        font-size: 32px;
        line-height: 32px;
        text-transform: uppercase;
        max-width: 164px;
        position: absolute;
        top: calc(50% - 64px/2 + 160px);
        left: 16.62%;
        z-index: 2;

        @media(max-width:767px) {
          font-size: 24px;
          line-height: 24px;
          max-width: 140px;
          top: calc(50% - 64px/2 + 25px);
          left: 3.62%;
        }
      }
    }
  }
}

.creations-item:last-child {
  display: flex;
  flex-wrap: wrap;
  gap: 28px;
  margin-top: 78px;

  @media(max-width:1200px) {
    gap: 35px;
  }

  @media(max-width:767px) {
    flex-wrap: nowrap;
    flex-direction: column;
    margin-top: 50px;
  }
}

.creations-item__image {
  transition: 0.2s ease;
  position: relative;
  cursor: pointer;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%);

  &_1 {
    background-image: url(../img/image_1.png);

    @media(max-width:767px) {
      background-position: left;
    }
  }

  &_2 {
    background-image: url(../img/image_2.png);

    @media(max-width:767px) {
      background-position: center -179px;
    }
  }

  &_3 {
    background-image: url(../img/image_3.png);
  }

  &_4 {
    background-image: url(../img/image_4.png);

    @media(max-width:767px) {
      background-position: center;
    }
  }

  &_5 {
    background-image: url(../img/image_5.png);
  }

  &_6 {
    background-image: url(../img/image_6.png);

    @media(max-width:767px) {
      background-position: center -145px;
    }
  }

  &_7 {
    background-image: url(../img/image_7.png);

    @media(max-width:767px) {
      background-position: center -40px;
    }
  }

  &_8 {
    background-image: url(../img/image_8.png);

    @media(max-width:767px) {
      background-position: center -75px;
    }
  }

  &:hover {
    &::after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      background: rgba(255, 255, 255, 0.75071);
    }

    color: $black;
  }
}