.footer {
    background-color: $black;
    padding-top: 44px;
    padding-bottom: 33px;
}

.footer-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 17px;

    @media(max-width:767px) {
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
    }
}

.footer-item:first-child {
    flex: 51%;
}

.footer-item {
    &:nth-child(1) {
        @media(max-width:767px) {
            order: -1;
            margin-bottom: 32px;
        }
    }

    &:nth-child(2) {
        @media(max-width:767px) {
            margin-bottom: 16px;
        }
    }

    &:nth-child(3) {
        @media(max-width:767px) {
            order: -1;
            margin-bottom: 38px;
        }
    }

    &>span {
        font-size: 15px;
        line-height: 25px;
        mix-blend-mode: normal;
        opacity: 0.5;
    }
}

.footer-icon {
    &__link {
        margin-right: 12px;
        position: relative;

        &:last-child {
            margin-right: 0;
        }

        &::after {
            content: '';
            display: block;
            position: absolute;
            bottom: -5px;
            left: calc(50% - 12px);
            height: 2px;
            width: 0;
            background-color: $white;
            transition: width 0.4s ease;
        }

        &:hover::after,
        &:focus:after {
            width: 24px;
        }
    }
}