.header {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 64px;

    &-item {
        &:first-child {
            z-index: 2;
        }
    }

    @media(max-width:425px) {
        padding-top: 40px;
    }
}

/* Desktop */
.menu {
    list-style-type: none;
    display: flex;

    @media(max-width:767px) {
        display: none;
    }
}

.nav {
    list-style-type: none;
    display: flex;

    @media(max-width:767px) {
        flex-direction: column;
    }

    &-item {
        margin-right: 32px;

        @media(max-width:767px) {
            margin-right: 0;
            margin-bottom: 16px;
            text-align: center;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    &-link {
        text-decoration: none;
        color: $white;
        font-family: 'Alata', sans-serif;
        font-size: 15px;
        line-height: 25px;
        position: relative;

        &::after {
            content: '';
            display: block;
            position: absolute;
            left: calc(50% - 12px);
            height: 2px;
            width: 0px;
            background-color: $white;
            transition: width 0.3s ease;
        }

        &:hover::after,
        &:focus:after {
            width: 24px;
        }
    }
}

/* Mobile*/
.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 1000;
}

.modal .modal_content {
    background-color: $black;
    padding: 40px 24px 223px 24px;
    width: auto;
    height: 53vh;
    z-index: 2;
}

.modal .modal_content .close_modal_window {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
}

.modal_content__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 159px;
}

.nav-mob {
    display: none;

    @media(max-width:767px) {
        display: block;
    }
}

.nav__wrapper {
    list-style-type: none;
}

.nav {
    &-item_mob {
        margin-bottom: 24px;
    }

    &-link_mob {
        font-family: 'Josefin Sans', sans-serif;
        text-decoration: none;
        color: $white;
        font-weight: 300;
        font-size: 24px;
        line-height: 25px;
        text-transform: uppercase;
    }
}

.btn_modal {
    display: none;

    @media (max-width: 767px) {
        display: block;
        background: none;
        border: none;
        cursor: pointer;
    }

    &:active,
    &:focus {
        outline: none;
    }
}